*, *::after, *::before{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    margin: 0;
    padding: 0;
    background: #68EDCB; 
    height: 100vh;
    color: black;
    font-family: 'Noto Sans', sans-serif;
}

.container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-container{
    background: white;
    border-radius: 5px;
}

.form-container form{
    width: 500px;
    font-size: 20px;
}

.header{
    font-size: 1.6rem;
    padding: 12px 0px 12px 30px;
}

.form-control{
    position: relative;
    margin-left: 50px;
}

.form-control input{
    display: block;
    margin-left: 50px;
    padding-left: 8px;
    font-size: 20px;
    width: 79%;
    height: 40px;
    outline: none;
    border: none;
    border-bottom: 1px solid #333
}

.form-control i{
    position: absolute;
    visibility: hidden;
}

.form-control .fa-check{
    color: #2EFF2E;
}

.form-control .fa-xmark{
    color: #ff0033;
}

.form-control small{
    margin-left: 50px;
    color: #ff0033;
    visibility: hidden;
}

.form-control .fa-solid{
    top: 50%;
    right: 20px;
}

.btn-container{
    display: flex;
    justify-content: space-around;
    margin: 1em 0;
}

.btn-container button{
    width: 80%;
    font-size: 1.4rem;
    padding: 8px 0;
    cursor: pointer;
    background:#60C370;
    color: #fff;
    outline: none;
    border: none;
    border-radius: 5px;
    box-shadow: 0 2px 2px #60A080;
}

.btn-container button:hover{
    background: #60E075;
}

.btn-container button:focus{
    box-shadow: none;
    transform: translate(0, 2px);
    transition: transform 0.1s;
    transition: box-shadow 0.1s;
}
